<template>
  <div >
    <div v-if="datos" class="container-fluid"  style="background: #e3e5eb; ">
      <div  style="background: #e3e5eb;">
        <Menu />
        <div  class="main-app animate-up col-lg-12" style="background: #e3e5eb;">
          <div id="aside" class="col-lg-3 col-md-4 col-sm-12 pl-0 " >
            <Navigation color="#2980b9" class="pt-0 pl-0"/>
          </div>
          <div class="col-lg-9 col-md-8 col-sm-12 pl-0 pr-0" >
            <Content class="pt-0 pl-0 pr-0 " style="min-height: calc(100vh - 70px);"/>
          </div>  
        </div>
      </div>
    </div>
    <Loader v-else />
  </div>
</template>

<script>
import Menu from "../../components/IntranetComponents/Menu";
import Navigation from "../../components/IntranetComponents/Navigation";
import Content from "../../components/IntranetComponents/Content";
import Loader from "../../components/Loader";

export default {
  components: {
    Menu,
    Navigation,
    Content,
    Loader
  },
  data: () => ({
    datos: false
  }),
  mounted(){
    setTimeout(() => {
      this.datos = true;
    }, 3000);
    this.$store.dispatch('getNavigations');
    this.$store.dispatch('getAnounces');
    

    /* this.$store.dispatch('getSlideImages');  */
    /* this.$store.dispatch('getCoursesHome'); */
    
  },
  methods: {}
};
</script>
<style>

.main-app {
  display: flex;
  flex-flow: row nowrap;
  width: 100%;
  margin-top: 70px;
  transition: ease-in .8s;
  /* animation: ease animatedUp .8s; */
}




.aside > .lista {
  margin-top: -70px;
}

.column-flex, .column-flex2 {
    display: flex;
    flex-flow: column nowrap;
}

@media screen and (max-width: 960px) {
  #aside {
    display: none;
  }
}


.animate-up {
  animation: ease animatedUp .8s;
}

@keyframes animatedUp {
    0% {
        opacity: 0;
        transform: translateY(0);
    }

    100% {
        opacity: 1;
        transform: translateY(20px);
    }
}



</style>