import Vue from 'vue'
import VueRouter from 'vue-router'
import Intranet from '../views/Intranet/Intranet.vue';
import LoginI from '../views/Intranet/Login';
import P404 from '../views/P404';
import store from '../store/index';

Vue.use(VueRouter)

const routes = [{
        path: '*',
        redirect: P404,
    },
    {
        path: '/app1/intranet/404',
        name: 'PageNotFound',
        component: P404
    },
    {
        path: '/app1/login',
        name: 'login-intranet',
        component: LoginI
    },
    /* ADMIN */
    {
        path: '/app1/aintranet',
        name: 'intranet',
        component: Intranet,
        children: [{
                path: '/app1/aintranet/',
                name: 'home-aintranet',
                component: () =>
                    import ('../views/Intranet/RoleAdministrador/Home'),
                beforeEnter: (to, from, next) => {
                    const role = store.getters.user.rol_usuario;
                    if (role === 'alumno' || role === 'docente') {
                        next('/app1/intranet/404');
                    } else {
                        next()
                    }
                }
            },
            {
                path: '/app1/aintranet/alumnos',
                name: 'alumnos-aintranet',
                component: () =>
                    import ('../components/IntranetComponents/Alumnos'),
                beforeEnter: (to, from, next) => {
                    const role = store.getters.user.rol_usuario;
                    if (role === 'alumno' || role === 'docente') {
                        next('/app1/intranet/404');
                    } else {
                        next()
                    }
                },
            },
            {
                path: '/app1/aintranet/alumnos/nuevo-alumno',
                name: 'nuevo-alumno-aintranet',
                component: () =>
                    import ('../components/IntranetComponents/FormUser'),
                beforeEnter: (to, from, next) => {
                    const role = store.getters.user.rol_usuario;
                    if (role === 'alumno' || role === 'docente') {
                        next('/app1/intranet/404');
                    } else {
                        next()
                    }
                }
            },
            {
                path: '/app1/aintranet/alumnos/:user_id',
                name: 'alumno-aintranet',
                component: () =>
                    import ('../views/Intranet/RoleAdministrador/Alumno'),
                beforeEnter: (to, from, next) => {
                    const role = store.getters.user.rol_usuario;
                    if (role === 'alumno' || role === 'docente') {
                        next('/app1/intranet/404');
                    } else {
                        next()
                    }
                }
            },
            {
                path: '/app1/aintranet/docentes',
                name: 'docentes-aintranet',
                component: () =>
                    import ('../components/IntranetComponents/Docentes'),
                beforeEnter: (to, from, next) => {
                    const role = store.getters.user.rol_usuario;
                    if (role === 'alumno' || role === 'docente') {
                        next('/app1/intranet/404');
                    } else {
                        next()
                    }
                }
            },
            {
                path: '/app1/intranet/myaccount',
                name: 'myaccount-aintranet',
                component: () =>
                    import ('../components/IntranetComponents/Account.vue'),
            },


        ],
        meta: {
            autenticate: true
        }
    },
    /* DOCENTE */
    {
        path: '/app1/dintranet',
        name: 'dintranet',
        component: Intranet,
        children: [{
                path: '/app1/dintranet/',
                name: 'home-dintranet',
                component: () =>
                    import ('../views/Intranet/RoleDocente/Home'),
            },

            {
                path: '/app1/intranet/myaccount',
                name: 'myaccount-dintranet',
                component: () =>
                    import ('../components/IntranetComponents/Account.vue'),
            },
        ],
        meta: {
            autenticate: true
        }

    },
    /* ALUMNO */
    {
        path: '/app1/aluintranet',
        name: 'aluintranet',
        component: Intranet,
        children: [{
                path: '/app1/aluintranet/',
                name: 'home-aluintranet',
                component: () =>
                    import ('../views/Intranet/RoleAlumno/Home'),
            },
            {
                path: '/app1/aluintranet/resumennotas',
                name: 'resumennotas-aluintranet',
                component: () =>
                    import ('../views/Intranet/RoleAlumno/Cursos'),
            },
            {
                path: '/app1/aluintranet/cursos',
                name: 'cursos-aluintranet',
                component: () =>
                    import ('../views/Intranet/RoleAlumno/MaterialCurso'),
            },
            {
                path: '/app1/aluintranet/cursos/curso',
                name: 'curso-aluintranet',
                component: () =>
                    import ('../views/Intranet/RoleAlumno/Curso'),
            },
            {
                path: '/app1/aluintranet/anuncios',
                name: 'anuncios-aluintranet',
                component: () =>
                    import ('../views/Intranet/RoleAlumno/Anuncios'),
            },
            {
                path: '/app1/aluintranet/horario',
                name: 'horario-aluintranet',
                component: () =>
                    import ('../views/Intranet/RoleAlumno/Horario'),
            },
            {
                path: '/app1/intranet/myaccount',
                name: 'myaccount-aluintranet',
                component: () =>
                    import ('../components/IntranetComponents/Account.vue'),
            },

        ],
        meta: {
            autenticate: true
        }

    },

]

const router = new VueRouter({
    /* mode: 'history', */
    base: process.env.BASE_URL,
    routes
})

router.beforeEach((to, from, next) => {
    let iToken = localStorage.getItem('iToken'); //CONFIRMA SI TENGO UN TOKEN EN LS
    let routeIsProtected = to.matched.some(record => record.meta.autenticate) //PREGUNTA SI ESTOY INTENTANDO ACCEDER A UNA RUTA QUE REQUIERE ESTAR AUTENTICADO
    const role = store.getters.user.rol_usuario;
    /*  let administratorRequired = to.matched.some(record => record.meta.administrator); */

    if (iToken == null && routeIsProtected) {
        next('/app1/login')
    } else if (iToken != null && !routeIsProtected && role === 'administrador') {
        next('/app1/aintranet')
    } else if (iToken != null && !routeIsProtected && role === 'docente') {
        next('/app1/dintranet')
    } else if (iToken != null && !routeIsProtected && role === 'alumno') {
        next('/app1/aluintranet')
    } else {
        next()
    }
})

export default router;