import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';
import Swal from 'sweetalert2'
Vue.use(Vuex);

/* DATA FICTICIA */
import { Notifications } from '../test/Notifications';
import { Courses, ListCoursesEnrolled, cursosMaterial, asistencia } from '../test/Courses';
import { Alumnos } from '../test/Alumnos';
import { Docentes } from '../test/Docentes';
import { Anounces } from '../test/Anounces';
import { ImagesSlide } from '../test/SlideHome';

/* Configs */
import { ListSignOut, AdminNavigation, TeacherNavigation, StudentNavigation, AdminCards, apiKeyClimaAdmin, AlumnoHomeCards } from '../test/Configs';
import { baseUrl } from '../config';

const configHeaderToken = (token) => {
    const config = {
        headers: {
            'Authorization': 'Bearer ' + token
        }
    }
    return config;
}

export default new Vuex.Store({
    state: {
        baseURL: baseUrl,
        auth_token: localStorage.getItem('iToken') || '',
        lista: 0,
        user: {},
        notifications: [],
        alumnos: {},
        docentes: {},
        configs: {
            ListSignOut: [],
            navigations: []
        },
        anounces: [],
        coursesHome: {},
        slideHome: [],
        cursos: {},
        cursosMaterial: {},
        asistencia: {
            series: [],
            labels: [],
            listaAsistencia: []
        },
        listCard: [],
        clima: {},
        configClima: {
            apiKeyClima: apiKeyClimaAdmin,
            city: 'Lima,PE'
        },
        basecolegio: {
            niveles: [],
            grados: [],
        },



        newUser: {},

        parentNewUser: {},


    },
    getters: {
        lista: (state) => {
            return state.lista;
        },
        user: (state) => {
            return state.user;
        },
        notifications: (state) => {
            return state.notifications;
        },
        slideHome: (state) => {
            return state.slideHome;
        },
        coursesHome: (state) => {
            return state.coursesHome;
        },
    },
    mutations: {
        setToken(state, token) {
            state.auth_token = token;
        },
        /* SETTERS CONFIGS */
        updateListMenu(state, index) {
            state.lista = index
        },
        setListSignOut(state, listso) {
            state.configs.ListSignOut = listso;
        },
        setNavigations(state, navigations) {
            state.configs.navigations = navigations;
        },
        setHomeCard(state, listCards) {
            state.listCard = listCards;
        },

        setBaseColegio(state, base) {
            state.basecolegio = base;
        },

        /* SETTER USUARIO */
        setUsuarioProfile(state, usuario) {
            state.user = usuario
        },

        /* SETTERS Notifications CRUD */
        setNotifications(state, notifications) {
            state.notifications = notifications;
        },

        /* SETTERS Cursos CRUD */
        setCourses(state, courses) {
            state.cursos = courses;
        },

        setCursosMaterial(state, listaCursosMaterial) {
            state.cursosMaterial = listaCursosMaterial;
        },

        /* SET CursosHome */
        setCoursesHome(state, coursesHome) {
            state.coursesHome = coursesHome;
        },

        /* SETTERS Alumnos CRUD */
        setAlumnos(state, alumnos) {
            state.alumnos = alumnos;
        },

        /* setNewUser(state, user) {
            state.alumnos = user
        }, */

        /* SETTERS Docentes CRUD */
        setDocentes(state, docentes) {
            state.docentes = docentes;
        },

        /* SETTERS Anounces CRUD */
        setAnounces(state, anounces) {
            state.anounces = anounces;
        },

        /* SETTERS SlideImages CRUD */
        setSlideImages(state, slideImages) {
            state.slideHome = slideImages;
        },

        /* SETTER ASISTENCIA */
        setAsistencia(state, Miasistencia) {
            state.asistencia = Miasistencia;
        },

        /* SETTER CLIMA */
        setClima(state, clima) {
            state.clima = clima;
        }

    },
    actions: {
        /* SIGNIN */
        async goSignIn({ commit, state }, usuario) {
            try {
                const { data } = await axios.post(`${state.baseURL}/api/auth/signin`, usuario);
                localStorage.setItem('iToken', data.auth_token);
                commit('setToken', data.auth_token);
                const config = configHeaderToken(data.auth_token);
                const res = await axios.get(`${state.baseURL}/api/auth/profile`, config);
                commit('setUsuarioProfile', res.data);
                return true;
            } catch (error) {
                Swal.fire({
                    icon: 'error',
                    title: 'Oops...',
                    text: 'Usuario o contraseña incorrectos!',
                })
                return false;
                /* return error; */
            }
        },

        logout({ commit }) {
            commit('setToken', '');
            localStorage.removeItem('iToken');
            this.$router.push('/app1/login');
        },

        /* ACTIONS CONFIGS CRUD */
        getListSignOut({ commit }) {
            commit('setListSignOut', ListSignOut);
        },
        getNavigations({ commit, state }) {
            const role = state.user.rol_usuario;
            if (role === 'administrador') {
                commit('setNavigations', AdminNavigation);
                commit('setHomeCard', AdminCards);
            } else if (role === 'docente') {
                commit('setNavigations', TeacherNavigation);
            } else {
                commit('setNavigations', StudentNavigation);
                commit('setHomeCard', AlumnoHomeCards);
            }
        },

        async getClimaAdmin({ commit, state }) {
            try {
                const climaLS = localStorage.getItem('clima');
                const millisActual = new Date().getTime();
                if (climaLS === null || climaLS.expiracion < millisActual) {
                    const fechaExpiración = new Date().getTime() + (1000 * 60 * 30); //media hora
                    const { data } = await axios.get('https://api.openweathermap.org/data/2.5/weather?lat=-12.04&lon=-77.03&appid=367f15fa5bac55eb17c75d6deb0713be')
                    const clima = {
                        temperatura: (parseInt(data.main.temp) - 273.15).toFixed(2),
                        humedad: data.main.humidity,
                        descripcion: data.weather[0].description,
                        expiracion: fechaExpiración
                    }
                    commit('setClima', clima);
                    localStorage.setItem('clima', JSON.stringify(clima))
                } else {
                    return commit('setClima', JSON.parse(climaLS));
                }
            } catch (error) {}
        },

        async getBaseColegio({ commit, state }) {
            try {
                const config = configHeaderToken(state.auth_token);
                const nivelesRes = (await axios.get(`${state.baseURL}/api/basecolegio/niveles`, config)).data;
                const gradosRes = (await axios.get(`${state.baseURL}/api/basecolegio/grados`, config)).data;
                let niveles = [],
                    grados = [];
                nivelesRes.forEach(nivel => {
                    niveles.push(nivel.nombre);
                });
                gradosRes.forEach(grado => {
                    grados.push({ nombre: grado.nombre, nivel: grado.nivel });
                });
                commit('setBaseColegio', { niveles, grados });
            } catch (error) {
                return;
            }
        },

        /* ACTIONS CRUD NOTIFICATIONS */
        getNotifications({ commit }) {
            commit('setNotifications', Notifications);
        },

        /* ACTIONS CRUD CURSOS */
        getCourses({ commit }) {
            setTimeout(() => {
                commit('setCourses', Courses);
            }, 1500)
        },
        addCourse({ commit, state }, course) {
            setTimeout(() => {
                const filtro = state.cursos.filtro;
                const cursos = state.cursos.cursos;
                cursos.push(course);
                commit('setCourses', { filtro, cursos });
            }, 1500)
        },
        getCoursesHome({ commit }) {
            setTimeout(() => {
                commit('setCoursesHome', ListCoursesEnrolled);
            }, 1500)
        },
        getCoursesMaterial({ commit }) {
            commit('setCursosMaterial', cursosMaterial);
        },

        /* ACTION ASISTENCIA */
        getAsistencia({ commit }) {
            commit('setAsistencia', asistencia);
        },

        /* ACTIONS CRUD ALUMNOS */
        async getAlumnos({ commit, state }) {
            try {
                const config = configHeaderToken(state.auth_token);
                const alumnos = (await axios.get(`${state.baseURL}/api/alumno`, config)).data.alumnos;
                commit('setAlumnos', { headers: Alumnos.headers, desserts: alumnos });
            } catch (error) {
                return;
            }

        },
        async getAlumnoPorId({ state }, user_id) {
            try {
                const config = configHeaderToken(state.auth_token);
                const alumno = (await axios.get(`${state.baseURL}/api/alumno/${user_id}`, config)).data.user
                alumno.fecha_nacimiento = alumno.fecha_nacimiento.substring(0, 10);
                return alumno;
            } catch (error) {
                return false;
            }
        },
        async addAlumno({ commit, state }, alumno) {
            try {
                console.log('post', alumno);
                const config = configHeaderToken(state.auth_token);
                const alumnoResponse = (await axios.post(`${state.baseURL}/api/user`, alumno, config)).data
                console.log('res', alumnoResponse);
                if (alumnoResponse) {
                    Swal.fire(
                        'Good job!',
                        'El alumno se guardó correctamente!',
                        'success'
                    )
                }
                return true;
            } catch (error) {
                console.log(error);
                return false;
            }
        },
        async editAlumno({ commit, state }, alumno) {
            try {
                const config = configHeaderToken(state.auth_token);
                const alumnoResponse = (await axios.put(`${state.baseURL}/api/user`, alumno, config)).data
                console.log('res', alumnoResponse);
                return true;
            } catch (error) {
                console.log(error);
                return false;
            }
        },

        /* ACTIONS CRUD DOCENTES */
        getDocentes({ commit }) {
            setTimeout(() => {
                commit('setDocentes', Docentes);
            }, 1500)
        },
        addDocente({ commit }, docente) {

        },

        /* ACTIONS CRUD ANOUNCES */
        getAnounces({ commit }) {
            setTimeout(() => {
                commit('setAnounces', Anounces);
            }, 1500)
        },
        addAnounce({ commit }, anounce) {

        },
        getSlideImages({ commit, state }) {
            setTimeout(() => {
                commit('setSlideImages', ImagesSlide);
                console.log(state.slideHome);
            }, 1500);
        },
        addSlideImage({ commit }, slideImage) {

        },
        async updatePasswordUser({ state }, user) {
            try {
                const config = configHeaderToken(state.auth_token);
                const alumnoResponse = (await axios.put(`${state.baseURL}/api/user/updatepassword`, user, config)).data
                if (alumnoResponse) {
                    return true;
                }
            } catch (error) {
                return false;
            }
        },
        updateGlobalIndex({ state, commit }) {
            try {
                const navigation = state.configs.navigations
                for (let i = 0; i < navigation.length; i++) {
                    if (window.location.hash === '#' + navigation[i].url) {
                        return commit('updateListMenu', i)
                    }
                }
            } catch (error) {

            }
        },
        updateEdad({ state }) {
            try {
                const anio_actual_millis = new Date().getTime();
                const fec_nac = new Date(state.user.fecha_nacimiento).getTime();
                const edad_millis = anio_actual_millis - fec_nac;
                const edad = Math.floor(edad_millis / (1000 * 60 * 60 * 24 * 365));
                state.user.edad = edad;
            } catch (error) {}
        },


    },
    modules: {}
})