<template>
  <div>
    <div class="loginBody" v-if="!charging">

      <div class="title-return">
        <a @click="returnHome">
          <i class="material-icons" style="color: #000;">arrow_back</i>
          <p>GO HOME</p>
        </a>
      </div>

      <img class="wave" src="../../assets/img/wave.png" />

      <img class="logo-cdt" src="../../assets/img/logo.png" />

      <div class="containerLogin">
        <div class="img">
          <img src="../../assets/img/exam.svg" />
        </div>
        <div class="login-content">
          <form v-on:submit.prevent="Ingresar">
            <img src="../../assets/img/avatar.svg" />
            <h2 class="title">Bienvenido</h2>
            <div v-if="errors || errors.length > 0">
                <div  v-for="(error, index) in errors" :key="index"  class="alert alert-danger alert-dismissible fade show" role="alert" style="margin: .8em;">
                    <strong> {{ error.name }} </strong>
                    <button type="button" class="close" data-dismiss="alert" aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
            </div>
            <div class="input-div one">
              <div class="i">
                <i class="fas fa-user"></i>
              </div>
              <div class="div">
                <h5>DNI</h5>
                <input id="dni-castleOfTalents" v-model="user.dni" type="text" class="inputLogin" />
              </div>
            </div>
            <div class="input-div pass mb-6">
              <div class="i">
                <i class="fas fa-lock"></i>
              </div>
              <div class="div">
                <h5>Password</h5>
                <input id="password-castleOfTalents" v-model="user.password" type="password" class="inputLogin" />
              </div>
            </div>
            <!-- <a href="#">Forgot Password?</a> -->
            <input @submit="Ingresar($event)" type="submit" class="btn" value="Login" />
          </form>
        </div>
      </div>
    </div>

    <Loader v-else />
  </div>
</template>

<script>
import Loader from "../../components/Loader";
import Swal from 'sweetalert2'

export default {
  components: {
    Loader
  },
  data: () => ({
    user:{
      dni: "",
      password: "",
    },
    show1: false,
    charging: false,
    errors: []
  }),
  updated(){
    const inputs = document.querySelectorAll(".inputLogin");
    inputs.forEach(input => {
      input.addEventListener("focus", function addcl() {
        let parent = input.parentNode.parentNode;
        parent.classList.add("focus");
      });
      input.addEventListener("blur", function remcl() {
        let parent = input.parentNode.parentNode;
        if (input.value == "") {
          parent.classList.remove("focus");
        }
      });
    });
  },
  mounted() {
    const inputs = document.querySelectorAll(".inputLogin");
    inputs.forEach(input => {
      input.addEventListener("focus", function addcl() {
        let parent = input.parentNode.parentNode;
        parent.classList.add("focus");
      });
      input.addEventListener("blur", function remcl() {
        let parent = input.parentNode.parentNode;
        if (input.value == "") {
          parent.classList.remove("focus");
        }
      });
    });
  },
  methods: {
    async Ingresar() {
      try {
        this.charging = true;
        const usuario = await this.$store.dispatch('goSignIn', this.user);
        if (usuario){;
          this.charging = false;
          if(usuario.rol_usuario === 'administrador'){
            this.$router.push("/app1/aintranet/");
          }else if(usuario.rol_usuario === 'docente'){
            this.$router.push("/app1/dintranet/");
          }else {
            this.$router.push("/app1/aluintranet/");
          }
        }else {
          this.charging = false;
          this.user.dni= '';
          this.user.password= '';
        }
      } catch (error) {
        this.user.dni= '';
        this.user.password= '';
        this.charging = false;
      }
    },
    returnHome() {
      this.$router.push("/");
    }
  }
};
</script>

<style>
.loginBody {
  font-family: "Poppins", sans-serif;
  position: relative;
  overflow: hidden;
  box-sizing: border-box;
}

.loginBody .wave {
  position: fixed;
  bottom: 0;
  left: 0;
  height: 100%;
  animation: ease-in animatedLeftToRight 1s;
  /* z-index: 1; */
}

.loginBody .containerLogin {
  width: 100vw;
  height: 100vh;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 7rem;
  padding: 0 2rem;
}

.loginBody .img {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  z-index: 10;
  animation: ease-in animatedUp 2s;
}

.loginBody .login-content {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  text-align: center;
  animation: ease-in animatedUp 2s;
}

.loginBody .img img {
  width: 500px;
} 

.login-content form {
  width: 360px;
}

.login-content img {
  height: 100px;
}

.login-content h2 {
  margin: 15px 0;
  color: #333;
  text-transform: uppercase;
  font-size: 2.9rem;
}

.login-content .input-div {
  position: relative;
  display: grid;
  grid-template-columns: 7% 93%;
  margin: 25px 0;
  padding: 5px 0;
  border-bottom: 2px solid #d9d9d9;
}

.login-content .input-div.one {
  margin-top: 0;
}

.i {
  color: #d9d9d9;
  display: flex;
  justify-content: center;
  align-items: center;
}

.i i {
  transition: 0.3s;
}

.input-div > div {
  position: relative;
  height: 45px;
}

.input-div > div > h5 {
  position: absolute;
  left: 10px;
  top: 50%;
  transform: translateY(-50%);
  color: #999;
  font-size: 18px;
  transition: 0.3s;
}

.input-div:before,
.input-div:after {
  content: "";
  position: absolute;
  bottom: -2px;
  width: 0%;
  height: 2px;
  background-color: #38d39f;
  transition: 0.4s;
}

.input-div:before {
  right: 50%;
}

.input-div:after {
  left: 50%;
}

.input-div.focus:before,
.input-div.focus:after {
  width: 50%;
}

.input-div.focus > div > h5 {
  top: -5px;
  font-size: 15px;
}

.input-div.focus > .i > i {
  color: #38d39f;
}

.input-div > div > input {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  border: none;
  outline: none;
  background: none;
  padding: 0.5rem 0.7rem;
  font-size: 1.2rem;
  color: #555;
  font-family: "poppins", sans-serif;
}

.input-div.pass {
  margin-bottom: 4px;
}

.loginBody a {
  display: block;
  text-align: right;
  text-decoration: none;
  color: #999;
  font-size: 0.9rem;
  transition: 0.3s;
}

.loginBody a:hover {
  color: #38d39f;
}

.loginBody .btn {
  display: block;
  width: 100%;
  height: 50px;
  border-radius: 25px;
  outline: none;
  border: none;
  background-image: linear-gradient(to right, #32be8f, #38d39f, #32be8f);
  background-size: 200%;
  font-size: 1.2rem;
  color: #fff;
  font-family: "Poppins", sans-serif;
  text-transform: uppercase;
  margin: 1rem 0;
  cursor: pointer;
  transition: 0.5s;
}
.loginBody .btn:hover {
  background-position: right;
}

.title-return {
  margin: 1.8em;
  z-index: 60;
  position: absolute;
  top: 15px;
  left: 10px;
  animation: ease-in animatedLeftToRight 2.5s;
}

.title-return a {
  text-decoration: none;
  color: #000;
  /* padding: 2em; */
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
}

.title-return a p {
  color: #000;
  margin-left: 10px;
  margin-bottom: 0;
}

.loginBody .logo-cdt {
    position: absolute;
    top: 15px;
    right: 20px;
    height: 100px;
    animation: ease-in animate 2.5s;
}

@keyframes animate {
  0% {
    opacity: 0;
    transform: translateX(800px);
  }

  100% {
    opacity: 1;
    transform: translate(0);
  }
}

@keyframes animatedDown {
  0% {
    opacity: 0;
    transform: translateY(-800px);
  }

  100% {
    opacity: 1;
    transform: translate(0);
  }
}

@keyframes animatedUp {
  0% {
    opacity: 0;
    transform: translateY(800px);
  }

  100% {
    opacity: 1;
    transform: translate(0);
  }
}

@keyframes animatedLeftToRight {
  0% {
    opacity: 0;
    transform: translateX(-800px);
  }

  100% {
    opacity: 1;
    transform: translate(0);
  }
}


@media screen and (max-width: 1050px) {
  .containerLogin {
    grid-gap: 5rem;
  }
}

@media screen and (max-width: 1000px) {
  .loginBody form {
    width: 290px;
  }

  .loginBody .login-content h2 {
    font-size: 2.4rem;
    margin: 8px 0;
  }

  .loginBody .img img {
    width: 400px;
  }
}

@media screen and (max-width: 900px) {
  .loginBody .containerLogin {
    grid-template-columns: 1fr;
  }

  .loginBody .img {
    display: none;
  }

  .loginBody .wave {
    display: none;
  }

  .loginBody .login-content {
    justify-content: center;
  }
}

@media screen and (max-width: 360px) {
    .loginBody .logo-cdt {
        height: 80px;
    }
    .loginBody .containerLogin {
        grid-gap: 0;
    }
    .loginBody form {
        width: 250px
    }
}
</style>