<template>

    <div class="container-fluid">
      <v-list-item id="item-avatar" class="mb-2">
        <v-list-item-avatar>
          <v-img :src="user.imagen"></v-img>
        </v-list-item-avatar>

        <v-list-item-content>
          <v-list-item-title>{{ user.nombres }} {{ user.apellido_paterno }} {{ user.apellido_materno }}</v-list-item-title>
          <v-list-item-subtitle>{{ user.dni }}</v-list-item-subtitle>
        </v-list-item-content>
      </v-list-item>
      
      <v-list  nav dense style=" border-radius: 10px; ">
        <v-list-item-group v-model="lista"  :mandatory="mandatory">
          <v-list-item :color="color" v-for="(item) in configs.navigations" :key="item.title" link   @click="redirectTo(item.url)">
            <v-list-item-icon>
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-item-icon>

            <v-list-item-content>
              <v-list-item-title>{{ item.title }}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list-item-group>
      </v-list>

      <Anounces  id="anounce-list"/>

    </div>

</template>

<script>
import { mapState, mapActions, mapGetters } from "vuex";
import Anounces from './Anounces';

export default {
  components: {
    Anounces
  },
  props: {
      toolbar: Boolean,
      color: String
  },
  data: () => ({
    globalIndex: 0,
    mandatory: true
  }),
  computed: {
    ...mapState(['user', 'configs']),
    ...mapGetters({
      listFromStore: 'lista'
    }),
    lista: {
      get() { return this.listFromStore },
      set(value) { return value }
    }
  },
  watch:{
    globalIndex: function (val) {
      this.$store.commit('updateListMenu', val)
    },
  },
  mounted(){
    this.updateGlobalIndex()
  },
  methods: {
    redirectTo( url ) {
      this.$router.push(url).catch(err => {});
    },
    updateGlobalIndex() {
      try {
        const navigation = this.configs.navigations
        for(let i=0; i < navigation.length; i++){
          if( window.location.hash === '#'+navigation[i].url ){
            this.mandatory = true
            return this.$store.commit('updateListMenu', i)
            
          }
        }
      } catch (error) {
        
      }
    }
  }
};
</script>

<style>
#item-avatar {
  background: rgba(41,128,185, 0.475);
  border: 1px solid rgba(235, 217, 217, 0.9);
  border-radius: 10px;
}
#item-avatar .v-list-item__content .v-list-item__title, #item-avatar .v-list-item__content .v-list-item__subtitle{
  color: #fafafa;
}

@media screen and (max-width: 960px) {
    #anounce-list {
        display: none;
    }
    #item-avatar {
      background: rgba(0,0,0, 0.475);
      border: 1px solid rgba(235, 217, 217, 0.9);
      border-radius: 10px;
    }
}
</style>