import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import vuetify from './plugins/vuetify';
import axios from 'axios';

import { baseUrl } from './config';

Vue.config.productionTip = false

const ignoreWarnMessage = 'The .native modifier for v-on is only valid on components but it was used on <div>.';
Vue.config.warnHandler = function(msg, vm, trace) {
    if (msg === ignoreWarnMessage) {
        msg = null;
        vm = null;
        trace = null;
    }
}

const userConected = async() => {
    try {
        const token = localStorage.getItem('iToken');
        if (!token) return null;
        const config = {
            headers: {
                'Authorization': 'Bearer ' + token
            }
        }
        const res = await axios.get(`${baseUrl}/api/auth/profile`, config);
        if (!res) return null;
        return res.data;
    } catch (error) {
        return null;
    }
}

const render = async() => {
    const usuario = await userConected();
    if (usuario === null) {
        localStorage.removeItem('iToken');
        router.push('/app1/login');
    } else {
        store.commit('setUsuarioProfile', usuario);
        store.dispatch('updateEdad');
    }
    new Vue({
        store,
        router,
        vuetify,
        render: h => h(App)
    }).$mount('#app')

}

render();